import React from 'react';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';

import { Loader, TextField, SelectField } from '../components';

import { LoadUpdate, LoadQuery } from '../api';
import { useSnackbar } from '../hooks';
import {
  UpdateLoad,
  LoadStatuses,
  LoadStatus,
  FormOnClose,
} from '../utils/dataTypes';
import { cleanObject } from '../utils/queryHelpers';

const validationSchema = yup.object({
  rate: yup.number().typeError('Rate must be a number'),
  totalCharges: yup
    .number()
    .typeError('Total charges must be a number')
    .required('Total charges is required'),
});

const startValues = {
  id: '',
  status: 'Available' as LoadStatus,
  // truckDeliveryMiles: undefined,
  // milesByRoads: undefined,
  rate: undefined,
  totalCharges: undefined,
  profit: undefined,
  rpm: undefined,
};

export default function SetRateInfoForm({
  id,
  onClose,
}: {
  id: string;
  onClose: FormOnClose;
}) {
  const [initialValues, setInitialValues] =
    React.useState<UpdateLoad>(startValues); // ToDo do like this in all forms (edit and view)
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    error,
    data,
  } = useQuery({
    queryKey: ['load', id],
    queryFn: LoadQuery,
    retry: false,
  });
  React.useEffect(() => {
    if (!error) {
      return;
    }
    setSnackbarMessage({
      severity: 'error',
      message: error instanceof Error ? error.message : JSON.stringify(error),
    });
  }, [error]);

  const { mutate, isLoading } = useMutation(LoadUpdate, {
    onSuccess: (data) => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Saved',
      });
      queryClient.invalidateQueries(['load', id]);
      queryClient.invalidateQueries(['loads']);
      onClose('Save', data);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateLoad>({
    initialValues,
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values);
    },
  });

  React.useEffect(() => {
    setInitialValues({
      ...initialValues,
      ...cleanObject(data, Object.keys(initialValues)),
    });
  }, [data]);

  React.useEffect(() => {
    if (
      Number.isFinite(Number(formik.values.totalCharges)) &&
      Number.isFinite(Number(formik.values.rate))
    ) {
      const profit =
        Math.round(
          ((formik.values.totalCharges as number) -
            (formik.values.rate as number)) *
            100,
        ) / 100;
      if (profit !== formik.values.profit) {
        console.log('GHGH', profit, formik.values.profit);
        formik.setFieldValue('profit', profit);
      }
    } else {
      formik.setFieldValue('profit', 0);
    }
  }, [formik.values.totalCharges, formik.values.rate]);

  React.useEffect(() => {
    if (
      Number.isFinite(Number(formik.values.rate)) &&
      formik.values.rate !== 0 &&
      Number.isFinite(Number(formik.values.truckDeliveryMiles)) &&
      Number.isFinite(Number(formik.values.milesByRoads))
    ) {
      const rpm =
        Math.round(
          (((formik.values.truckDeliveryMiles as number) +
            (formik.values.milesByRoads as number)) /
            (formik.values.rate as number)) *
            100,
        ) / 100;
      if (rpm !== formik.values.rpm) {
        console.log('KIKI', rpm, formik.values.rpm);
        formik.setFieldValue('rpm', rpm);
      }
    } else {
      formik.setFieldValue('rpm', 0);
    }
  }, [formik.values.rate]);

  /*const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    console.error(formik.errors);
    formik.handleSubmit(e);
  };*/

  const handleClose = () => {
    onClose('Close');
  };

  /*const handleReset = (e: any) => {
    formik.handleReset(e);
  };*/

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              maxHeight: 'calc(100% - 70px)',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box key="topControls" sx={{ marginBottom: -1, p: 1 }}>
              <SelectField
                fieldName="status"
                label="Status"
                items={LoadStatuses}
                /*disabledItems={LoadStatuses.filter(
                (status) =>
                  status !== 'Available' && status !== 'Not Available',
              )}*/
                disabled
                formik={formik}
              />
            </Box>
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box key="left" sx={{ p: 1, width: '50%' }}>
                {/*<TextField
                  fieldName="truckDeliveryMiles"
                  label="Delivery Miles"
                  disabled
                  formik={formik}
                />*/}
                <TextField
                  fieldName="rate"
                  label="Drivers Rate"
                  placeholder="Enter drivers rate"
                  number
                  formik={formik}
                />
                <TextField
                  fieldName="profit"
                  label="Profit"
                  number
                  disabled
                  formik={formik}
                />
              </Box>
              <Box key="right" sx={{ p: 1, width: '50%' }}>
                {/*<TextField
                  fieldName="milesByRoads"
                  label="Miles by roads"
                  disabled
                  formik={formik}
                />*/}
                <TextField
                  fieldName="totalCharges"
                  label="Total Rate"
                  placeholder="Enter total rate"
                  number
                  formik={formik}
                />
                <TextField
                  fieldName="rpm"
                  label="RPM"
                  number
                  disabled
                  formik={formik}
                />
              </Box>
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
                disabled={!formik.dirty}
              >
                Save
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading || loading || fetching} />
    </Box>
  );
}
